import React, {useState, useEffect} from "react";



const SearchBar = ({handleSubmit}) => {

  const [search, setSearch] = useState('')
  const [query, setQuery] = useState('Kitesurfing')
 
  const updateSearch = (e) =>{
    setSearch(e.target.value)
    };

    const getSearch = (e) => {
      e.preventDefault()
      if (search !== ''){
      setQuery(search)
      setSearch('')
      }
      };

   useEffect(() => {
   handleSubmit(query)
   }, [query])
  
  return (
    <div>
       <form className='search-form' onSubmit={getSearch}>
         <input className='search-bar' id='searchBar' type='text' placeholder='Search for YouTube videos' value={search} onChange={updateSearch}>
         </input>
            <button className='search-button' type='submit'>
               Search
            </button>
       </form>
    </div>
  );
}

export default SearchBar;

