import React from "react";
import '../App.css'

const VideoItem = ({ video, onVideoSelect }) => {
const pTime = video.snippet.publishTime;
const corrTime = "Publish Time: " + pTime.slice(0, 10)
return (
 <div className='videoItem' onClick={() => onVideoSelect(video)}>
        <h1>{video.snippet.title}</h1>
        <h2>{corrTime}</h2>
        <img className='image' src={video.snippet.thumbnails.medium.url} alt='YT video'></img>
        <p>{video.snippet.description}</p>
 </div>
  );
}

export default VideoItem;

