import React from "react";
import VideoItem from "./VideoItem";
import '../App.css'


const VideoList = ({ videos, onVideoSelect }) => {
  const listOfVideos = videos.map((video,id) =>
  <VideoItem
      onVideoSelect={onVideoSelect}
      key={id}
      video={video}
  />
  );
  return (
    <div>
      {listOfVideos}
    </div>
  );
}

export default VideoList;